<template>
<div class="search">
  <div class="search-box-wrapper">
    <search-box ref="searchBox" @query="onQueryChange"></search-box>
  </div>
  <div ref="shortcutWrapper" class="shortcut-wrapper" v-show="!query">
    <scroll class="shortcut" ref="shortcut" :data="shortcut">
      <div class="">
        <div class="hot-key">
          <h1 class="title">热门搜索</h1>
          <ul v-show="hotKey">
            <li class="item" v-for="item in hotKey" @click="addQuery(item.k)">
              <span>{{item.k}}</span>
            </li>
          </ul>
        </div>
        <div class="search-history" v-show="searchHistory.length > 0">
          <h1 class="title">
            <span class="text">搜索历史</span>
            <span class="clear">
              <i class="icon-clear"></i>
            </span>
          </h1>
          <search-list :searches="searchHistory"></search-list>
        </div>
      </div>
    </scroll>
  </div>
  <div class="search-result" ref="searchResult" v-show="query">
    <suggest ref="suggest" :query="query" @onScrollStart="hideKeyboard" @selectSearch="saveHistory"></suggest>
  </div>
  <router-view></router-view>
</div>
</template>

<script type="text/ecmascript-6">
import {ERR_OK} from 'api/config'
import SearchBox from 'base/search-box/search-box'
import {getHotKey} from 'api/search'
import suggest from 'components/suggest/suggest'
import searchList from 'base/search-list/search-list'
import {mapActions,mapGetters} from 'vuex'
import {playlistMixin} from 'common/js/mixin'
import scroll from 'base/scroll/scroll'
export default {
  mixins:[playlistMixin],
  data() {
    return {
      hotKey: [],
      query:''
    }
  },
  computed: {
    shortcut(){
      return this.hotKey.concat(this.searchHistory)
    },
    ...mapGetters([
      'searchHistory'
    ])
  },
  created() {
    this._getHotKey()
  },
  methods: {
    handlerList(playlist){
      const bottom = playlist.length >0 ? '60px' : 0
      this.$refs.shortcutWrapper.style.bottom = bottom
      this.$refs.shortcut.refresh()
      this.$refs.searchResult.style.bottom = bottom
      this.$refs.suggest.refresh()
    },
    _getHotKey() {
      getHotKey().then((res) => {
        if (res.code === ERR_OK) {
          this.hotKey = res.data.hotkey.slice(0, 10)
        }
      })
    },
    addQuery(item){
      this.$refs.searchBox.setQuery(item)
    },
    onQueryChange(query){
      this.query= query
    },
    hideKeyboard(){
      this.$refs.searchBox.inputBlur()
    },
    saveHistory(){
      this.setHistory(this.query)
    },
    ...mapActions([
      'setHistory'
    ])

  },
  watch: {

  },
  components: {
    SearchBox,
    suggest,
    searchList,
    scroll
  }
}
</script>

<style lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"

  .search
    .search-box-wrapper
      margin: 20px
    .shortcut-wrapper
      position: fixed
      top: 178px
      bottom: 0
      width: 100%
      .shortcut
        height: 100%
        overflow: hidden
        .hot-key
          margin: 0 20px 20px 20px
          .title
            margin-bottom: 20px
            font-size: $font-size-medium
            color: $color-text-l
          .item
            display: inline-block
            padding: 5px 10px
            margin: 0 20px 10px 0
            border-radius: 6px
            background: $color-highlight-background
            font-size: $font-size-medium
            color: $color-text-d
        .search-history
          position: relative
          margin: 0 20px
          .title
            display: flex
            align-items: center
            height: 40px
            font-size: $font-size-medium
            color: $color-text-l
            .text
              flex: 1
            .clear
              extend-click()
              .icon-clear
                font-size: $font-size-medium
                color: $color-text-d
    .search-result
      position: fixed
      width: 100%
      top: 178px
      bottom: 0
</style>



