<template>
  <transition name="slide">
    <music-list :title="title" :singer-img="singerImg" :songs="songs" :rank="rank"></music-list>
  </transition>
</template>

<script type="text/ecmascript-6">
  import MusicList from 'components/music-list/music-list'
  import {getMusicList} from 'api/rank'
  import {mapGetters} from 'vuex'
  import {ERR_OK} from 'api/config'
  import {createSong} from 'common/js/song'
  export default {
    computed: {
      title(){
        return this.topList.topTitle
      },
      singerImg(){
        if(this.songs.length > 0){
          return this.songs[0].image
        }else {
          return ''
        }
      },
      ...mapGetters([
        'topList'
      ])
    },
    data() {
      return {
        songs:[],
        rank:true
      }
    },
    created() {
      this._getMusicList()
    },
    methods: {
      _getMusicList(){
        if(!this.topList.id){
          this.$router.push({
            path:'/rank'
          })
        }
        getMusicList(this.topList.id).then((res) => {
          if(res.code === ERR_OK){
            this.songs = this._normaLizeMusicList(res.songlist)
            console.log(this.songs);
          }
        })
      },
      _normaLizeMusicList(list){
        let ret = []
        list.forEach((item) => {
          if(item.data.songid && item.data.albumid){
            ret.push(createSong(item.data))
          }
        })
        return ret
      }
    },
    components: {
      MusicList
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  .slide-enter-active, .slide-leave-active
    transition: all 0.3s ease

  .slide-enter, .slide-leave-to
    transform: translate3d(100%, 0, 0)
</style>



