<template>
  <scroll ref="suggest"
          class="suggest"
          :data="result"
          :pullup="pullup"
          :beforeScroll="beforeScroll"
          @beforeScroll="hideKeyboard"
          @scrollToEnd="searchMore"
  >
    <ul class="suggest-list">
      <li class="suggest-item" v-for="item in result" @click="selectItem(item)">
        <div class="icon">
          <i :class="getIconCls(item)"></i>
        </div>
        <div class="name">
          <p class="text" v-html="getDisplayName(item)"></p>
        </div>
      </li>
      <loading v-show="hasMore" title=""></loading>
    </ul>
    <no-result v-show="!hasMore && result.length <=0" title="抱歉，搜索结果为空！"></no-result>
  </scroll>
</template>

<script type="text/ecmascript-6">
  import {getSearchList} from 'api/search'
  import {ERR_OK} from 'api/config'
  import {createSong} from 'common/js/song'
  import scroll from 'base/scroll/scroll'
  import loading from 'base/loading/loading'
  import Singer from 'common/js/singer'
  import {mapMutations,mapActions} from 'vuex'
  import NoResult from 'base/no-result/no-result'
  const TYPE_SINGER = 'singer'
  const perpage = 20

  export default {
    props: {
      query:{
        type:String,
        default:''
      },
      showSinger:{
        type:Boolean,
        default:true
      }
    },
    data() {
      return {
        page:1,
        result:[],
        hasMore:true,
        pullup:true,
        beforeScroll:true
      }
    },
    methods: {
      hideKeyboard(){
        this.$emit('onScrollStart')
      },
      refresh(){
        this.$refs.suggest.refresh()
      },
      _getSearchList(){
        this.page = 1
        this.hasMore = true
        this.$refs.suggest.scrollTo(0,0,0)
        getSearchList(this.query,this.page,this.showSinger,perpage).then((res) => {
          if(res.code === ERR_OK){
            this.result = this._genResult(res.data)
            this._checkMore(res.data)
          }
        })
      },
      _genResult(data) {
        let ret = []
        if (data.zhida && data.zhida.singerid) {
          ret.push({...data.zhida, ...{type: TYPE_SINGER}})
        }
        if (data.song) {
          ret = ret.concat(this._normalize(data.song.list))
        }
        return ret
      },
      _normalize(list){
        let ret = []
        list.forEach((musicData) => {
          ret.push(createSong(musicData))
        })
        return ret
      },
      _checkMore(data){
        let song = data.song
        if(!song.list || (song.curnum + song.curpage*perpage) >= song.totalnum){
          this.hasMore = false
        }
      },
      searchMore(){
        if(!this.hasMore){
          return
        }
        this.page++
        getSearchList(this.query,this.page,this.showSinger,perpage).then((res) => {
          if(res.code === ERR_OK){
            this.result = this.result.concat(this._genResult(res.data))
            this._checkMore(res.data)
          }
        })
      },
      selectItem(item){
        if(item.type === TYPE_SINGER){
          const singer = new Singer({
            id:item.singermid,
            name:item.singername
          })
          this.$router.push({
            path: `/search/${singer.id}`
          })
          this.setSinger(singer)
        }else {
          this.insertSong(item)
        }
        this.$emit('selectSearch')
      },
      getDisplayName(item){
        if(item.type === TYPE_SINGER){
          return item.singername
        }else {
          return `${item.name}-${item.singer}`
        }
      },
      getIconCls(item){
        if(item.type === TYPE_SINGER){
          return 'icon-mine'
        }else {
          return 'icon-music'
        }
      },
      ...mapMutations({
        setSinger:'SET_SINGER'
      }),
      ...mapActions([
        'insertSong'
      ])
    },
    watch: {
      query(){
        this._getSearchList()
      }
    },
    components: {
      scroll,
      loading,
      NoResult
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"

  .suggest
    height: 100%
    overflow: hidden
    .suggest-list
      padding: 0 30px
      .suggest-item
        display: flex
        align-items: center
        padding-bottom: 20px
      .icon
        flex: 0 0 30px
        width: 30px
        [class^="icon-"]
          font-size: 14px
          color: $color-text-d
      .name
        flex: 1
        font-size: $font-size-medium
        color: $color-text-d
        overflow: hidden
        .text
          no-wrap()
    .no-result-wrapper
      position: absolute
      width: 100%
      top: 50%
      transform: translateY(-50%)
</style>



